<template>
  <div class="components">
    <h3></h3>
    <h4>multiple</h4>
    <p> <code>multiple</code> true,</p>
    <h4>multiple-size</h4>
    <p><code>multiple-size</code></p>
    <h4></h4>
    <div class="center">
      <vue-core-image-upload
        :class="['btn', 'btn-primary']"
        :crop="false"
        @imageuploaded="imageUploded"
        :max-file-size="5242880"
        :multiple="true"
        :multiple-size="4"
        url="http://101.198.151.190/api/upload2.php" >
      </vue-core-image-upload>
    </div>
    <table class="m-table bordered">
      <thead>
        <tr>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in fileList" :key="item.name">
          <td>{{item.name}}</td>
          <td>{{item.size}}</td>
        </tr>
      </tbody>
    </table>
    <p></p>
    <h4>Code Example</h4>
    <pre v-highlightjs><code class="javascript">&lt;vue-core-image-upload
:class="['btn', 'btn-primary']"
:crop="false"
@imageuploaded="imageUploded"
:max-file-size="5242880"
:multiple="true"
:multiple-size="4"
url="http://101.198.151.190/api/upload2.php" &gt;
&lt;/vue-core-image-upload&gt;</code></pre>
  <a href="https://github.com/Vanthink-UED/vue-core-image-upload/blob/master/site/client/components/doc/cn/MultipleFile.vue"></a>
  </div>
</template>

<script>
import VueCoreImageUpload from 'vue-core-image-upload'
export default {
  components: {
    VueCoreImageUpload,
  },
  data() {
    return {
      fileList: [],
    }
  },

  methods: {
    imageUploded(res) {
      if(res.errcode == 0) {
        this.fileList = res.data;
      }
    }
  }

};

</script>



